import React, { Component } from "react"
import { REMOVE_IF_JS_CLASSNAME } from "../html";
import Loadable from "@loadable/component"

const RemoveClassname = () => {
  debugger
  Array.from(document.getElementsByClassName(REMOVE_IF_JS_CLASSNAME)).forEach(function(el) {
    el.classList.remove(REMOVE_IF_JS_CLASSNAME);
  })
  return null
}

const LoadableRemoveClassname = Loadable(() => import("./RemoveClassname"))

export default LoadableRemoveClassname
